import React, { useEffect, useState } from "react";
import "../styles/pages/receipes.scss";
import Card from "../components/Card";
import SearchBar from "../components/SearchBar";
import Dialog from "../components/Dialog";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import defaultCategories from '../assets/data/categories.json'; 
import defaultReceipes from '../assets/data/receipes.json'; 

function ReceipeList() {
  const [isOpen, setIsOpen] = useState(false);
  const [categories, setCategories] = useState(defaultCategories);
  const [currentCategory, setCurrentCategories] = useState("");
  const [receipes, setReceipes] = useState(defaultReceipes);

  let title = 'All';
  if(categories){
    title = categories.find(each=>each.key==currentCategory);
    title=title?title.name:'All';
  }
  const navigate = useNavigate();
  useEffect(() => {
    const fetchData = async () => {
      try {
        const categoriesList = await axios.get("/categories");
        setCategories(categoriesList.data);
      } catch (error) {
        console.error("Error occured", error);
      }
    };
    fetchData();
  }, []);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const receipes = await axios.get("/receipes/" + currentCategory);
        setReceipes(receipes.data);
      } catch (error) {
        console.error("Error occured", error);
      }
    };
    fetchData();
  }, [currentCategory]);

  return (
    <section className="container">
      {isOpen ? <Dialog setIsOpen={setIsOpen} /> : null}
      <div className="header-shadow"></div>
      <div className="filters">
        Category
        <SearchBar />
        <i
          className="fa-solid fa-circle-plus"
          //onClick={() => setIsOpen(!isOpen)}
          onClick={() => navigate("/create-new-receipe")}
        ></i>
      </div>
      <hr></hr>

      <aside>
        <ul>
          {categories.map((each) => (
            <li
              key={each.key}
              id={each.key}
              onClick={(e) => setCurrentCategories(e.target.id)}
            >
              {each.name}
            </li>
          ))}
        </ul>
      </aside>
      <h1>{title}</h1>
      <div className="content">
        {receipes.map(each => (
          <Card key={each.id} {...each}/>
        ))}
      </div>
    </section>
  );
}
ReceipeList.defaultProps = {
  title: 'Default Title',
  description: 'This is the default description.',
};

export default ReceipeList;
